var that;
export default {
	data() {
		return {
			intervalID: 0,
			havetime: 60,
			zhanghao: '',
			mima0: '',
			mima1: '',
			mima2: '',
			strtime: '获取验证码',
			state: 0

		}
	},
	onLoad: function(e) {
		that = this;
	},
	onShow: function(res) {

	},
	onUnload() {
		console.log('页面卸载')
		clearInterval(that.intervalID)
	},
	methods: {

		lijidenglu: function() {
			if (!that.zhanghao) {
				uni.showToast({
					title: '请输入账号'
				})

				return
			}
			if (!that.mima0) {
				uni.showToast({
					title: '请输入验证码'
				})

				return
			}
			if (!that.mima1) {
				uni.showToast({
					title: '请输入密码'
				})
			
				return
			}
			if (!that.mima2) {
				uni.showToast({
					title: '请输入确认密码'
				})
			
				return
			}
			if (that.mima2 != that.mima1) {
				uni.showToast({
					title: '两次密码不一致'
				})
			
				return
			}
			uni.navigateBack({
				delta: 1
			})
		},

		yanzhengma: function() {
			console.log(that.zhanghao)
			if (that.state == 1) {
				return
			}
			if (!that.zhanghao) {
				uni.showToast({
					title: '请输入账号'
				})
				return
			}
			that.state = 1;
			that.intervalID = setInterval(function() {
				that.havetime--;
				if (that.havetime > 0) {
					that.strtime = that.havetime + 'S后获取'
				} else {
					clearInterval(that.intervalID)
					that.state = 0;
					that.havetime = 60;
					that.strtime = '获取验证码'
				}
			}, 1000)

		},

	}

}
